import config from "../config";
export default {
  props: {
    pos: {
      type: Object,
      required: true
    },
    arrivalDate: {
      type: Date,
      required: true
    },
    padding: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      config
    };
  },
  computed: {
    dateFormatted() {
      return dayjs(this.arrivalDate).format("HH:mm");
    },
    baseX() {
      return this.pos.end.x - this.padding.x;
    },
    baseY() {
      return this.pos.start.y + this.padding.y;
    }
  }
};